import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import IconButton from '@material-ui/core/IconButton';
import MenuItem from '@material-ui/core/MenuItem';
import { fetchSongs } from '../actions/action';
import FavoriteBar from './favoriteBar';
import './menuBar.css'

const useStyles = makeStyles((theme) => ({
    grow: {
        flexGrow: 1,
    },
    search: {
        position: 'relative',
        marginLeft: 0,
        width: '100%',
        [theme.breakpoints.up('sm')]: {
            marginLeft: theme.spacing(3),
            width: 'auto',
        },
    }
}));

export default function IonSearchbar(props) {
    // Controlamos la entrada
    const [searchTerm, setSearchTerm] = React.useState("");
    const handleChange = event => {
        setSearchTerm(event.target.value);
    };

    const handleKeyPress = (event) => {
        if (event.key === 'Enter') {
            startSearch();
        }
    }

    const startSearch = () => {
        props.params.dispatch(fetchSongs(searchTerm));
    }

    const classes = useStyles();

    // Logica visual iconos busqueda
    const clearIcon = document.querySelector(".clear-icon");
    const searchBar = document.querySelector(".searchIn");

    if (searchBar) {
        searchBar.addEventListener("keyup", () => {
            if (searchBar.value && clearIcon.style.visibility !== "visible") {
                clearIcon.style.visibility = "visible";
            } else if (!searchBar.value) {
                clearIcon.style.visibility = "hidden";
            }
        });
    }

    if (clearIcon) {
        clearIcon.addEventListener("click", () => {
            searchBar.value = "";
            clearIcon.style.visibility = "hidden";
        })
    }

    return (
        <div className={classes.grow}>
            <AppBar position="static">
                <Toolbar>
                    <IconButton
                        edge="start"
                        className={classes.menuButton}
                        color="inherit"
                        aria-label="">
                        <img alt="Logo" src={process.env.PUBLIC_URL + "/LogoTop.png"} className="menuLogo" />
                    </IconButton>
                    <div className={classes.search}>
                        <div className="wrapper">
                            <img className="search-icon"
                                onClick={startSearch}
                                alt="Search Icon"
                                src="data:image/svg+xml;utf8;base64," />
                            <input className="searchIn" placeholder="Search" type="text" value={searchTerm}
                                onChange={handleChange} onKeyPress={handleKeyPress} />
                            <img className="clear-icon" alt="Clear Icon" src="data:image/svg+xml;utf8;base64," />
                        </div>
                    </div>
                    <div className={classes.grow} />
                    <MenuItem>
                        <FavoriteBar props={props.favs} />
                    </MenuItem>
                </Toolbar>
            </AppBar>
        </div>
    );
}
